export default {
	data() {
		return {
			oldpassword: '',
			newpassword:'',
			rennewpassword:'',
			disable:true,
			loading:false
		}
	},
	watch:{//监听属性
		oldpassword(val){
			console.log(val);
			this.wa();
		},
		newpassword(val){
			this.wa();
		},
		rennewpassword(val){
			this.wa();
		}
	},
	methods:{
		wa(){//监听input框内容是否为空
			if(this.oldpassword==''||this.newpassword==""||this.rennewpassword==''){
				this.disable=true;
				return false;
			}else if(this.newpassword!==this.rennewpassword){
				this.disable=true;
				return false;
			}
			this.disable=false;
		},
		check(){//输入框判断
			//输入框都等于空
			if(this.oldpassword==''){
				uni.showToast({
					title: '请输入旧密码',
					icon:'none'
				});
				return false;
			}
			if(this.newpassword==''){
				uni.showToast({
					title: '请输入新密码',
					icon:'none'
				});
				return false;
			}
			if(this.rennewpassword==''){
				uni.showToast({
					title: '请输入确认密码',
					icon:'none'
				});
				return false;
			}
			//判断新密码两次输入的是否一致
			if(this.newpassword!==this.rennewpassword){
				uni.showToast({
					title: '两次密码输入不一致',
					icon:'none'
				});
				return false;
			}
		},
		sub(){//提交
			this.loading=true;
			this.disable=true;
			setTimeout(()=>{
				uni.showToast({
					title: '提交成功',
					icon:'success'
				});
				this.loading=false;
				this.disable=false;
			},2000)
		}
	}
}
